import React, { useState, useEffect } from 'react';
import Typewriter from 'typewriter-effect';
import Fade from 'react-reveal';
import endpoints from '../constants/endpoints';
import About from './About';
import Skills from './Skills';
import Projects from './Projects';
import Education from './Education';
import Experience from './Experience';
import Social from './Social';
import FallbackSpinner from './FallbackSpinner';

const styles = {
  nameStyle: {
    fontSize: '3.5em',
  },
  inlineChild: {
    display: 'inline-block',
  },
  mainContainer: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
};

function Home() {
  const [data, setData] = useState(null);

  useEffect(() => {
    fetch(endpoints.home, {
      method: 'GET',
    })
      .then((res) => res.json())
      .then((res) => setData(res))
      .catch((err) => err);
  }, []);

  return data ? (
    <Fade>
      <div style={styles.mainContainer}>
        <br />
        <h1 style={styles.nameStyle}>{data?.name}</h1>
      </div>
      <div style={{ flexDirection: 'row' }}>
        <h2 style={styles.inlineChild}>&nbsp;</h2>
        <Typewriter
          options={{
            loop: true,
            autoStart: true,
            strings: data?.roles,
          }}
        />
      </div>
      <Social />
      <Skills />
      <div style={{ flexDirection: 'row' }}>
        <br />
        <h3 id="projects" style={styles.nameStyle}>Projects</h3>
        <h3 style={styles.inlineChild}>Currently working toward adding more projects.</h3>
      </div>
      <Projects />
      <div style={{ flexDirection: 'row' }}>
        <h5 id="about" style={styles.nameStyle}>About</h5>
        <h6 style={styles.inlineChild}>TL;DR: I am Sam a Data, Tech, and Biz enthusiast!</h6>
      </div>
      <About />
      <div style={{ flexDirection: 'row' }}>
        <h7 id="education" style={styles.nameStyle}>Education</h7>
      </div>
      <Education />
      <div style={{ flexDirection: 'row' }}>
        <h8 id="experience" style={styles.nameStyle}>Experience</h8>
      </div>
      <Experience />
    </Fade>
  ) : <FallbackSpinner />;
}

export default Home;
